import "./Header.css";
import logo from "../assets/logo.png";
import { useNavigate } from "react-router-dom";

const Header = () => {
  let navigate = useNavigate();

  return (
    <div className="nav-style">
      <div className="logoContainer">
        <img
          onClick={() => navigate("/")}
          src={logo}
          className="logo"
          alt="logo"
        ></img>
      </div>
      <div className="holderListeGen">
        <ul className="listeGen">
          <li>Gnration 1</li>
          <li>Gnration 2</li>
          <li>Gnration 3</li>
          <li>Gnration 4</li>
          <li>Gnration 5</li>
          <li>Gnration 6</li>
          <li>Gnration 7</li>
          <li>Gnration 8</li>
        </ul>
      </div>
    </div>
  );
};

export default Header;
